body {
    box-sizing: border-box;
    height: 100vh;
    background: #87a8d81a;
}
* {
    margin: 0;
    padding: 0;
}

.btn {
    width: 50px;
    height: 50px;
    background: inherit;
    font-size: 18px;
    font-family: monospace;
    cursor: pointer;
}
.center {
    display: flex;
    justify-content: center;
}



.center p {
    margin-bottom: 10px;
    font-size: 28px;
    font-family: monospace;
    padding: 0 5px;
}





/* home css  */

.home-section {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.list {
    margin-top: 20px;
    margin-bottom: 20px;
}


.home-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: monospace;
}
.hom-box-h1 {
    margin-top: 50px;
    font-size: 50px;
}
.home-box-h1 {
    margin-top: 10px;
    margin-bottom: 15px;
}



.table-center {
    display: flex;
    justify-content: center;
    align-items: center;
}



input[type='number']{
    width: 50px;
    height: 25px;
    border: 1px solid grey;
    margin-left: 5px;
    padding-left: 5px;
    font-size: 18px;
}
label {
    font-family: monospace;
    font-size: 15px;
    margin-right: 30px;
}

input[type='number']:focus {
    border: 2px solid green;
    outline: none;
    
}

button[type='submit'] {
    width: 100px;
    height: 40px;
    border: none;
    background: #f25757;
    cursor: pointer;
    font-family: monospace;
    color: honeydew;
    font-size: 14px;
   
}

button[type='submit']:focus {
    outline: none;
    background: #f35656c5;
}
